@import url(https://fonts.googleapis.com/css2?family=Courgette&display=swap);
body {
  margin: 0 0;
  background-color: rgb(1, 0, 53);
  color: white;
  font-family: "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell",
    "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.main-content {
  margin: 3rem 5%;
}

section > h2 {
  text-align: center;
}

section {
  margin: 6rem auto;
}

ul {
  list-style-type: none;
  padding: 0;
}

#intro {
  display: flex;
  grid-column-gap: 1rem;
  -webkit-column-gap: 1rem;
          column-gap: 1rem;
  align-items: center;
  margin: 4rem auto;
}
#intro > :first-child {
  /* border: 1px solid red; */
  flex: 2 1;
}

#intro > :first-child p {
  margin: auto 5%;
}

#intro > :last-child {
  /* border: 1px solid red; */
  flex: 3 1;
  display: flex;
  flex-direction: column;
  grid-row-gap: 1rem;
  row-gap: 1rem;
}

#intro > :last-child h1 {
  margin: 0 auto;
  font-size: 2.5rem;
}

#intro > :last-child p {
  margin: 0 auto;
  font-size: 1.2em;
}
#intro #card {
  width: 90%;
  margin: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  grid-gap: 1rem;
  gap: 1rem;
  border-radius: 24px;
  box-shadow: 0px 0px 18px 5px rgba(255, 0, 0, 0.7);
}

#card p {
  font-size: 18px;
  text-align: center;
}

#intro img {
  margin-top: 2rem;
  border-radius: 50%;
  width: 40%;
  height: auto;
  animation: glow 1s infinite alternate, rotation 12s infinite linear;
}

#wave {
  display: inline-block;
  animation-name: wave;
  animation-duration: 2.5s;
  animation-iteration-count: infinite;
}

@keyframes glow {
  from {
    box-shadow: 0px 0px 12px 1px rgba(255, 0, 0, 1);
  }
  to {
    box-shadow: 0px 0px 34px 5px rgba(255, 0, 0, 1);
  }
}

@keyframes rotation {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(359deg);
  }
}

@keyframes wave {
  0% {
    transform: rotate(0deg);
  }
  10% {
    transform: rotate(-10deg);
  }
  20% {
    transform: rotate(12deg);
  }
  30% {
    transform: rotate(-10deg);
  }
  40% {
    transform: rotate(9deg);
  }
  50% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(0deg);
  }
}
@media screen and (max-width: 950px) {
  #intro {
    flex-direction: column;
    grid-row-gap: 2rem;
    row-gap: 2rem;
    text-align: center;
  }

  #intro img {
    width: 30%;
  }
}

@media screen and (max-width: 490px) {
  #intro img {
    width: 40%;
  }

  #intro > :last-child h1 {
    font-size: 2rem;
  }

  #intro > :last-child p {
    font-size: 1em;
  }
}

@media screen and (max-width: 400px) {
  #intro img {
    width: 50%;
  }

  #intro > :last-child h1 {
    font-size: 1.3rem;
  }

  #intro > :last-child p {
    font-size: 0.8rem;
  }
}

.contact {
  animation-name: appear;
  animation-duration: 2s;
}

@keyframes appear {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

form {
  margin: 0 auto;
  text-align: center;
}

input,
textarea {
  width: 60%;
  margin: 0.5rem auto 0.5rem auto;
  border-radius: 5px;
  transition: 0.4s;
}

input {
  text-align: center;
  height: 2rem;
}

textarea {
  height: 6rem;
  font-size: 16px;
  font-family: inherit;
  resize: vertical;
  text-indent: 8px;
  padding-top: 14px;
}

input:focus,
textarea:focus {
  box-shadow: 0px 0px 34px 0 rgba(255, 0, 0, 0.9);
  transition: 0.4s;
}

::-webkit-input-placeholder {
  color: gray;
}

:-ms-input-placeholder {
  color: gray;
}

::-ms-input-placeholder {
  color: gray;
}

::placeholder {
  color: gray;
}

.error {
  border: 2.5px solid red;
}

button {
  border: none;
  border-radius: 5px;
  font-size: 24px;
  display: block;
  width: 20%;
  height: 2rem;
  margin: 0.5rem auto 0.5rem auto;
  /* transition: 0.6s; */
}

button.enabled {
  cursor: pointer;
  background-color: white;
  box-shadow: 0px 0px 14px 0 rgba(255, 0, 0, 0.9);
}

button.disabled {
  cursor: not-allowed;
  background-color: grey;
}
button.enabled:hover {
  color: red;
  box-shadow: 0px 0px 24px rgba(255, 0, 0, 0.9);
  background-color: white;
  /* transition: 0.6s; */
}

.hidden {
  display: none;
}

.message {
  height: 2rem;
  width: 60%;
  margin: 1rem auto 0.5rem auto;
  display: table;
  font-size: 0.8rem;
}

.sent {
  display: table-cell;
  vertical-align: middle;
  text-align: center;
  border-radius: 5px;
  background-color: green;
}

.not-sent {
  display: table-cell;
  vertical-align: middle;
  text-align: center;
  border-radius: 5px;
  background-color: red;
}
/********************* media  **************************/

@media screen and (max-width: 900px) {
  input,
  textarea,
  .message {
    width: 90%;
  }

  button {
    width: 50%;
  }
}

@media screen and (max-width: 700px) {
  input,
  textarea,
  .message {
    width: 100%;
  }
}

@media screen and (max-width: 500px) {
  button {
    width: 100%;
  }
}

.footer {
  background-color: black;
  margin-bottom: 0;
  text-align: center;
  padding-top: 1rem;
  padding-bottom: 1rem;
}

.rights {
  font-size: 12px;
  color: gray;
  margin-bottom: 0;
  line-height: 35px;
}

.rights > span {
  color: red;
}

ul.icons {
  list-style: none;
  padding-left: 0;
}

ul.icons li {
  display: inline-block;
}

ul.icons a {
  color: gray;
  display: inline-block;
  width: 2em;
  font-size: 1.25em;
  text-align: center;
  border: 0;
  -webkit-filter: drop-shadow(0px 0px 9px red);
          filter: drop-shadow(0px 0px 9px red);
  transition: 0.6s;
}

ul.icons a:hover {
  -webkit-filter: drop-shadow(0px 0px 11px red);
          filter: drop-shadow(0px 0px 11px red);
}

.twitter:hover {
  color: #00acee;
  transition: 0.6s;
}

.github:hover {
  color: white;
  transition: 0.6s;
}

.mail:hover {
  color: #bb001b;
}

.youtube:hover {
  color: #ff0000;
}

#heart {
  width: 30px;
  color: #bb001b;
  margin-bottom: -2px;
  animation-name: heart-beat;
  animation-duration: 0.2s;
  animation-iteration-count: infinite;
  animation-direction: alternate;
}

@keyframes heart-beat {
  from {
    font-size: 15px;
  }
  to {
    font-size: 20px;
  }
}

.prjs {
  display: flex;
  flex-direction: column;
  /* justify-content: space-around; */
  /* align-content: space-around; */
  /* column-gap: 1rem; */
  grid-row-gap: 4rem;
  row-gap: 4rem;
  margin: 3rem auto;
}

.prj {
  display: flex;
  align-items: center;
  grid-column-gap: 1rem;
  -webkit-column-gap: 1rem;
          column-gap: 1rem;
}

.prj > :first-child {
  flex: 2 1;
}
.prj > :last-child {
  display: flex;
  flex-direction: column;
  /* row-gap: 1rem; */
  flex: 3 1;
}

#projects #wrapper {
  margin: auto;
  width: 90%;
  overflow: hidden;
  box-shadow: 0px 0px 18px 5px rgba(255, 0, 0, 0.7);
  border-radius: 24px;
}

img {
  object-fit: cover;
  width: 100%;
  height: 100%;
  border-radius: 5px;
}

.prj > p {
  font-size: 0.5em;
}

ul.links {
  display: flex;
  /* justify-content: left; */
  grid-column-gap: 1rem;
  -webkit-column-gap: 1rem;
          column-gap: 1rem;
}

ul.links a {
  color: white;
  transition: 0.3s;
}

ul.links a:hover {
  color: red;
  transition: 0.3s;
}

ul.tools {
  display: flex;
  grid-column-gap: 1rem;
  -webkit-column-gap: 1rem;
          column-gap: 1rem;
}

@media screen and (max-width: 950px) {
  .prj {
    flex-direction: column;
    grid-row-gap: 1rem;
    row-gap: 1rem;
    text-align: center;
  }
  ul.tools,
  ul.links {
    justify-content: center;
  }
}

#techs .logos {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  grid-gap: 2rem;
  gap: 2rem;
  margin: 3rem auto;
  text-align: center;
}

#techs .logos > * :first-child {
  padding: 1rem 1rem;
  font-size: 5rem;
  border-radius: 12px;
  box-shadow: 0px 0px 18px 5px rgba(255, 0, 0, 0.7);
  transition: 0.3s;
}

#techs .logos > :nth-child(1) :first-child {
  color: #0064a5;
}

#techs .logos > :nth-child(2) :first-child {
  color: #3c873a;
}
#techs .logos > :nth-child(3) :first-child {
  color: #e535ab;
}
#techs .logos > :nth-child(4) :first-child {
  color: #61dbfb;
}
#techs .logos > :nth-child(5) :first-child {
  color: #e34c26;
}
#techs .logos > :nth-child(6) :first-child {
  color: #264de4;
}

#techs .logos > * > :first-child:hover {
  box-shadow: 0px 0px 38px 12px rgba(255, 0, 0, 0.7);
  transition: 0.3s;
}

@media screen and (max-width: 560px) {
  #techs .logos > * :first-child {
    font-size: 3rem;
    box-shadow: 0px 0px 8px 3px rgba(255, 0, 0, 0.7);
  }
}

@media screen and (max-width: 430px) {
  #techs .logos > * :first-child {
    font-size: 2rem;
    padding: 0.5rem 0.5rem;
  }

  #techs .logos > * > p {
    font-size: 12px;
  }
}

@media screen and (max-width: 200px) {
  #techs .logos > * :first-child {
    font-size: 1.5rem;
    padding: 0.5rem 0.5rem;
  }

  #techs .logos > * > p {
    font-size: 12px;
    color: yellow;
  }
}

#quote {
  background-color: black;
  text-align: center;
  padding: 1.5rem 10%;
  font-family: "Courgette", cursive;
}

#quote > p {
  margin: 0;
  /* font-style: italic; */
}
#quote span {
  color: red;
}

#quote > #author {
  display: block;
  float: right;
  font-size: 14px;
}

